'use client';

import React, { useCallback, useEffect, useState } from 'react';
import { Button } from '@/components/ui/button';
import { useWebViewBridge } from '@/hooks/use-webview-bridge';
import { isMobileDevice, isValidBase64Image, uploadImageToServer } from '@/lib/image-utils';
import { Camera, Upload } from 'lucide-react';
import { cn } from '@/lib/utils';

interface ImageInputProps {
  onImageSelect?: (file: File | string) => void;
  onImageUpload?: (url: string) => void;
  uploadUrl?: string;
  className?: string;
  accept?: string;
  multiple?: boolean;
  disabled?: boolean;
  placeholder?: string;
}

export function ImageInput({
  onImageSelect,
  onImageUpload,
  uploadUrl,
  className,
  accept = 'image/*',
  multiple = false,
  disabled = false,
  placeholder = 'Ïù¥ÎØ∏ÏßÄÎ•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî',
}: ImageInputProps) {
  const [isUploading, setIsUploading] = useState(false);
  const [uploadError, setUploadError] = useState<string | null>(null);

  const { isWebView, requestImageUpload, requestCamera } = useWebViewBridge();
  const isMobile = isMobileDevice();
  const showMobileButtons = isMobile && isWebView;

  const handleWebViewImageResponse = useCallback(
    async (event: MessageEvent) => {
      if (event.data?.type === 'IMAGE_UPLOAD_RESPONSE') {
        const { imageData } = event.data;

        if (!imageData || !isValidBase64Image(imageData)) {
          setUploadError('Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ïù¥ÎØ∏ÏßÄ Îç∞Ïù¥ÌÑ∞ÏûÖÎãàÎã§.');
          return;
        }

        setUploadError(null);

        if (uploadUrl) {
          setIsUploading(true);
          try {
            const result = await uploadImageToServer(imageData, uploadUrl);
            if (result.success && result.url) {
              onImageUpload?.(result.url);
            } else {
              setUploadError(result.error || 'ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
            }
          } catch {
            setUploadError('ÏóÖÎ°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
          } finally {
            setIsUploading(false);
          }
        } else {
          onImageSelect?.(imageData);
        }
      }
    },
    [uploadUrl, onImageSelect, onImageUpload]
  );

  useEffect(() => {
    if (showMobileButtons) {
      window.addEventListener('message', handleWebViewImageResponse);
      return () => {
        window.removeEventListener('message', handleWebViewImageResponse);
      };
    }
  }, [showMobileButtons, handleWebViewImageResponse]);

  const handleFileSelect = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const files = event.target.files;
      if (!files || files.length === 0) return;

      const file = files[0];
      if (uploadUrl) {
        setIsUploading(true);
        const formData = new FormData();
        formData.append('file', file);

        fetch(uploadUrl, {
          method: 'POST',
          body: formData,
        })
          .then((response) => response.json())
          .then((result) => {
            if (result.url) {
              onImageUpload?.(result.url);
            } else {
              throw new Error('ÏóÖÎ°úÎìú ÏùëÎãµÏóê URLÏù¥ ÏóÜÏäµÎãàÎã§.');
            }
          })
          .catch((error) => {
            setUploadError('ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§: ' + error.message);
          })
          .finally(() => {
            setIsUploading(false);
          });
      } else {
        onImageSelect?.(file);
      }

      event.target.value = '';
    },
    [uploadUrl, onImageSelect, onImageUpload]
  );

  if (showMobileButtons) {
    return (
      <div className={cn('flex gap-2', className)}>
        <Button
          type="button"
          variant="outline"
          onClick={requestImageUpload}
          disabled={disabled || isUploading}
          className="flex items-center gap-2"
        >
          <Upload size={16} />
          Í∞§Îü¨Î¶¨
        </Button>
        <Button
          type="button"
          variant="outline"
          onClick={requestCamera}
          disabled={disabled || isUploading}
          className="flex items-center gap-2"
        >
          <Camera size={16} />
          Ïπ¥Î©îÎùº
        </Button>
        {isUploading && <div className="text-muted-foreground text-sm">ÏóÖÎ°úÎìú Ï§ë...</div>}
        {uploadError && <div className="text-sm text-red-500">{uploadError}</div>}
      </div>
    );
  }

  return (
    <div className={cn('w-full', className)}>
      <input
        type="file"
        id="image-input"
        accept={accept}
        multiple={multiple}
        onChange={handleFileSelect}
        disabled={disabled || isUploading}
        className="hidden"
      />
      <label
        htmlFor="image-input"
        className={cn(
          'flex h-32 w-full cursor-pointer flex-col items-center justify-center rounded-lg border-2 border-dashed border-gray-300 bg-gray-50 hover:bg-gray-100',
          disabled && 'cursor-not-allowed opacity-50',
          'transition-colors'
        )}
      >
        <div className="flex flex-col items-center justify-center pt-5 pb-6">
          <Upload className="mb-4 h-8 w-8 text-gray-500" />
          <p className="mb-2 text-sm text-gray-500">
            <span className="font-semibold">ÌÅ¥Î¶≠ÌïòÏó¨ ÏóÖÎ°úÎìú</span>
          </p>
          <p className="text-xs text-gray-500">{placeholder}</p>
        </div>
      </label>
      {isUploading && <div className="text-muted-foreground mt-2 text-sm">ÏóÖÎ°úÎìú Ï§ë...</div>}
      {uploadError && <div className="mt-2 text-sm text-red-500">{uploadError}</div>}
    </div>
  );
}
